@media (min-width:1700px) {
  .skills.list ul li {
    width: 33.333%
  }
}

@media (max-width:720px) {
  .skills ul {
    margin: -20px 0 0 0
  }
  .skills ul li {
    padding: 20px 0 0 0
  }
  .skills.dotted ul li {
    width: 100%
  }
  .skills.circles ul li {
    width: 50%
  }
  .skills.list ul li {
    width: 100%
  }
  .skills.list ul li:last-child,
  .skills.list ul li:last-child .name {
    margin-bottom: 0
  }
}

.clients-items {
  margin: -35px -35px 0 -35px;
  font-size: 0
}

.clients-items .clients-col {
  padding: 35px 35px 0 35px;
  display: inline-block;
  vertical-align: top;
  width: 25%
}

.clients-item {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100px;
  opacity: .4;
  transition: opacity 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -moz-transition: opacity 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -webkit-transition: opacity 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -o-transition: opacity 1s cubic-bezier(.165, .85, .45, 1) 0s
}

.clients-item a {
  text-decoration: none
}

.clients-item img {
  max-width: 100%;
  max-height: 100px
}

.clients-item:hover {
  opacity: 1
}

@media (max-width:720px) {
  .clients-items {
    margin: -20px -20px 0 -20px
  }
  .clients-items .clients-col {
    padding: 20px 20px 0 20px;
    width: 50%
  }
}

.service-items {
  margin-top: -70px;
  font-size: 0;
  text-align: center
}

.service-items .service-col {
  margin-top: 70px;
  display: inline-block;
  vertical-align: top;
  width: 50%
}

.service-items .service-col:nth-child(odd) .content-box {
  margin-right: 35px
}

.service-items .service-col:nth-child(even) .content-box {
  margin-left: 35px
}

.service-items .service-item .icon {
  font-size: 36px;
  line-height: normal;
  color: #72ff4b
}

.service-items .service-item .name {
  margin: 15px 0;
  color: #eee;
  font-weight: 500
}

@media (max-width:1199px) {
  .service-items {
    margin-top: -40px
  }
  .service-items .service-col {
    margin-top: 40px
  }
  .service-items .service-col:nth-child(odd) .content-box {
    margin-right: 20px
  }
  .service-items .service-col:nth-child(even) .content-box {
    margin-left: 20px
  }
}

@media (max-width:720px) {
  .service-items .service-col {
    width: 100%
  }
  .service-items .service-col:nth-child(odd) .content-box {
    margin-right: 0
  }
  .service-items .service-col:nth-child(even) .content-box {
    margin-left: 0
  }
