html {
  margin-right: 0!important
}

body {
  margin: 0;
  padding: 0;
  border: none;
  font-family: Palatino;
  font-size: 16px;
  line-height: 1.5;
  color: #bbb;
  background: #101010;
  letter-spacing: 0;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility
}

body.scroll_hidden {
  overflow: hidden;
  height: 100vh
}

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box
}

.clear {
  clear: both
}

.cursor-follower {
  margin: -13px 0 0 -13px;
  position: absolute;
  background: #72ff4b;
  width: 26px;
  height: 26px;
  -webkit-transition: transform 1s cubic-bezier(.02, 1.2, .8, 1), opacity .25s ease;
  transition: transform 1s cubic-bezier(.02, 1.2, .8, 1), opacity .25s ease;
  opacity: .4;
  z-index: 1111;
  pointer-events: none;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  -khtml-border-radius: 50%
}

.cursor-follower.hide {
  opacity: 0!important
}

@media (max-width:1199px) {
  .cursor-follower {
    display: none
  }
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  z-index: 11111
}

.preloader .spinner {
  position: absolute;
  left: 50%;
  top: 0;
  width: 1px;
  height: 100%
}

.preloader .spinner .double-bounce1,
.preloader .spinner .double-bounce2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%
}

.preloader .spinner .double-bounce2 {
  background: rgba(255, 255, 255, .5);
  height: 0;
  -webkit-animation: loading 2s infinite ease-in-out;
  animation: loading 2s infinite ease-in-out
}

.container {
  position: relative;
  margin: 0;
  overflow: hidden
}

.container:before {
  pointer-events: none;
  z-index: 12
}

.lines {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  font-size: 0;
  pointer-events: none;
  z-index: 1111
}

.lines.no-lines .line-col:before {
  opacity: 0
}

.lines .line-col {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 20%;
  height: 100vh
}

.lines .line-col:first-child:before {
  background: 0 0
}

.lines .line-col:first-child:after {
  left: auto;
  right: 0;
  width: 1000%
}

.lines .line-col:first-child .line {
  display: none
}

.lines .line-col:last-child:after {
  left: 0;
  right: auto;
  width: 1000%
}

.lines .line-col:nth-child(1):before {
  -o-transition-delay: 2.9s;
  -webkit-transition-delay: 2.9s;
  transition-delay: 2.9s
}

.lines .line-col:nth-child(1):after {
  top: 0;
  bottom: auto;
  -webkit-transition-delay: .9s;
  -o-transition-delay: .9s;
  transition-delay: .9s
}

.lines .line-col:nth-child(2):before {
  -o-transition-delay: 2.7s;
  -webkit-transition-delay: 2.7s;
  transition-delay: 2.7s
}

.lines .line-col:nth-child(2):after {
  top: auto;
  bottom: 0;
  -webkit-transition-delay: .7s;
  -o-transition-delay: .7s;
  transition-delay: .7s
}

.lines .line-col:nth-child(3):before {
  -o-transition-delay: 2.2s;
  -webkit-transition-delay: 2.2s;
  transition-delay: 2.2s
}

.lines .line-col:nth-child(3):after {
  top: 0;
  bottom: auto;
  -webkit-transition-delay: .2s;
  -o-transition-delay: .2s;
  transition-delay: .2s
}

.lines .line-col:nth-child(4):before {
  -o-transition-delay: 2.5s;
  -webkit-transition-delay: 2.5s;
  transition-delay: 2.5s
}

.lines .line-col:nth-child(4):after {
  top: auto;
  bottom: 0;
  -webkit-transition-delay: .5s;
  -o-transition-delay: .5s;
  transition-delay: .5s
}

.lines .line-col:nth-child(5):before {
  -o-transition-delay: 3s;
  -webkit-transition-delay: 3s;
  transition-delay: 3s
}

.lines .line-col:nth-child(5):after {
  top: 0;
  bottom: auto;
  -webkit-transition-delay: 1s;
  -o-transition-delay: 1s;
  transition-delay: 1s
}

.lines .line-col:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 0%;
  background: rgba(255, 255, 255, .07);
  transition: height 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -moz-transition: height 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -webkit-transition: height 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -o-transition: height 1s cubic-bezier(.165, .85, .45, 1) 0s
}

.lines .line-col:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #1e1e1e;
  z-index: 12;
  transition: height 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -moz-transition: height 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -webkit-transition: height 1s cubic-bezier(.165, .85, .45, 1) 0s;
  -o-transition: height 1s cubic-bezier(.165, .85, .45, 1) 0s
}

.lines.finish .line-col:before {
  height: 100%
}

.lines.finish .line-col:after {
  height: 0%
}

.lines.ready {
  z-index: 11
}

.wrapper {
  position: relative
}

.breadcrumbs {
  margin: 0
}

.lines-off .lines .line-col:before {
  display: none
}

.lines-on .lines .line-col:before {
  display: block
}

@media (max-width:1199px) {
  .lines .line-col {
    width: 33.333%
  }
  .lines .line-col:nth-child(4),
  .lines .line-col:nth-child(5) {
    display: none
  }
}

@media (max-width:720px) {
  .lines .line-col {
    width: 50%
  }
  .lines .line-col:nth-child(3),
  .lines .line-col:nth-child(4),
  .lines .line-col:nth-child(5) {
    display: none
  }
}

